.contacts {
  margin: 0;
  padding: 0;
}

.contacts__advertisement {
  margin: 0;
  padding: 0;
  font-family: $large-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: $gray-color;
}

.contacts__address {
  margin: 0;
  padding: 0;
  line-height: 20px;
}

.contacts__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 26px 20px;
  display: flex;
  justify-content: space-between;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    padding: 0 31px;
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    padding: 0 110px;
  }
}
